@import "_reset";
@import "_variables";

/*---GENERAL STYLES---*/
	h1, h2, h3, h4 {
		text-transform: uppercase;
		font-family: $branding-semibold;
		font-weight: normal;
		text-align: center;
		width: 100%;
		white-space: normal;
	}
	h1 {
		font-size: 50px;
	}
	h2 {
		font-size: 35px;
	}
	h3 {
		font-size: 25px;
	}
	h4 {
		font-size: 18px;
	}
	section {
		position: relative;
	}
	a:hover {
		font-weight: bold;
	}
	p {
		line-height: 25px;
		white-space: normal;
	}
	body.rtl {
		h1, h2, h3, h4, h5, p, a, span, ul, input, textarea, .footer_item, .table, .application_steps_wrap, .documents_block, .rules_list, .select_input_wrap, .tab_content {
			direction: rtl;
		}
	}
	main {
		min-height: -webkit-calc(100vh - 70px);
		min-height: calc(100vh - 70px);
	}
	.animate {
	    -webkit-transition: all linear 0.3s;
	    -o-transition: all linear 0.3s;
	    transition: all linear 0.3s;
	}
	/* .noscroll {
		overflow: hidden;
	} */
	.container {
		width: 100%;
		height: 100%;
		max-width: 1260px;
		padding-left: 15px;
		padding-right: 15px;
		margin: auto;
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
	}
	.btn {
		display: block;
		margin: auto;
		margin-top: 40px;
		cursor: pointer;
		width: 370px;
		line-height: 90px;
		font-size: 25px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		background: $accent;
		color: $white;
		text-transform: uppercase;
		font-family: $branding-semibold;
		position: relative;
		@extend.animate;
		.btn_icon {
			/* position: absolute;
			height: 34px;
			width: 34px !important;
					    top: 50%;
					    right: 30px;
					    -webkit-transform: translateY(-50%);
					    -ms-transform: translateY(-50%);
					    transform: translateY(-50%);
			 */
			font-size: 24px;
		    border: 2px solid #fff;
		    -webkit-border-radius: 50%;
		    border-radius: 50%;
		    height: 30px;
		    width: 30px;
		    position: absolute;
		    line-height: 30px;
		    height: 34px;
		    width: 34px;
		    top: 50%;
		    right: 30px;
		    -webkit-transform: translateY(-50%);
		    -ms-transform: translateY(-50%);
		    transform: translateY(-50%);
		}
		&:hover {
			background: $white;
			color: $accent;
			.btn_icon {
				border-color: $accent;
			}
		}
	}
	.rtl {
		.btn {
			.btn_icon {
				left: 30px;
				right: auto;
				-webkit-transform: translateY(-50%) scale(-1, 1);
				-ms-transform: translateY(-50%) scale(-1, 1);
				transform: translateY(-50%) scale(-1, 1);
			}
		}
	}
	/*---MOBILE MENU---*/
	.sandwich{
		display: none;
		position: relative;
		cursor: pointer;
		float: right;
		width: 70px;
	    height: 70px;
	    padding: 20px;
	    margin-right: -15px;
	    z-index: 1;
		.piece{
			position: relative;
			width: 100%;
			height: 4px;
			background-color: $black;
			@extend .animate;
			&.top{
				top: 20%;
			}
			&.middle{
				top: 40%;
			}
			&.bottom{
				opacity: 1;
				top: 60%;
				@extend .animate;
			}
		}
	}
	.sandwich.active{
		.piece{
			background: $accent;
			&.top{
				top: 50%;
				-webkit-transform: rotate(135deg);
				-ms-transform: rotate(135deg);
				transform: rotate(135deg);
			}
			&.middle{
				top: 39%;
				-webkit-transform: rotate(-135deg);
				-ms-transform: rotate(-135deg);
				transform: rotate(-135deg);
			}
			&.bottom{
				top: 0;
				opacity: 0;
				-webkit-transform: rotate(90deg);
				-ms-transform: rotate(90deg);
				transform: rotate(90deg);
			}
		}
	}
	/*---DECOR LOGO BLOCK---*/
		.decor_logo {
			position: relative;
			width: 86px;
			margin: 20px auto;
		    text-align: center;
			img {
				height: 50px;
			}
			&:before,
			&:after {
				content: '';
				position: absolute;
				background: $accent;
				width: 70px;
				height: 2px;
			    top: 50%;
			    -webkit-transform: translateY(-50%);
			    -ms-transform: translateY(-50%);
			    transform: translateY(-50%);
			}
			&:before {
				right: 100%;
			}
			&:after {
				left: 100%;
			}
		}
	/*---DECOR SQUARE BLOCK---*/
		.decor_block {
			border: 6px solid $accent;
			color: $white;
			padding: 15px;
			background: #2e2720;
		}
	/*---DECOR TEXT BLOCK---*/
