/*---HEADER---*/
	.header {
		height: 100px;
	    position: absolute;
	    z-index: 3;
	    width: 100%;
	    top: 0;
	    background: rgba(255, 255, 255, 0.6);
		.container {
			-webkit-box-pack: justify;
			-webkit-justify-content: space-between;
			-ms-flex-pack: justify;
			justify-content: space-between;
		    -webkit-box-align: center;
		    -webkit-align-items: center;
		    -ms-flex-align: center;
		    align-items: center;
		}
		.logo {
			height: 65px;
			img {
				display: inline-block;
				height: 100%;
				max-width: 100%;
				margin-right: 40px;
			    width: auto;
			}
		}
		.menu {
			width: -webkit-calc(100% - 420px);
			width: calc(100% - 420px);
		}
		.menu_wrap {
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-pack: end;
			-webkit-justify-content: flex-end;
			-ms-flex-pack: end;
			justify-content: flex-end;
			@extend .animate;
		}
		.nav_menu {
			.sub_menu_item_block, .menu-item-has-children {
				&:after {
					content: '\f105';
	  				/* font: 20px FontAwesome; */
	  				font-size: 20px;
					font-family: 'Font Awesome 5 Free';
				    font-weight: 900;
				    position: absolute;
				    right: 15px;
				    top: 10px;
				}
				&.revert:after {
					right: auto;
					left: 12px;
					-webkit-transform: scale(-1, 1);
					-ms-transform: scale(-1, 1);
					transform: scale(-1, 1);
				}
			}
			.sub_menu_item, .sub-menu {
				display: none;
				li {
					display: block;
				    margin: 0;
				    width: 200px;
				}
				a, span {
					display: block;
				    padding: 10px 30px;
				    cursor: pointer;
				    &:hover {
				    	background: #000;
				    }
				}
			}
			li {
				display: inline-block;
			    margin-left: 20px;
			    margin-right: 20px;
			    position: relative;
			    text-transform: uppercase;
			    &:first-of-type {
			    	/* margin-left: 0; */
			    }
			    &:last-of-type {
			    	/* margin-right: 0; */
			    }
			    .sub_menu_item, .sub-menu {
		    		position: absolute;
				    background: rgba(0, 0, 0, 0.8);
				    color: $white;
				    left: 0;
				    top: 30px;
				    &:before {
				    	content: '';
				    	position: absolute;
				    	width: 100%;
				    	height: 15px;
				    	top: -14px;
				    }
				    &:after {
				    	content: '';
				    	position: absolute;
				    	left: 50%;
					    -webkit-transform: translateX(-50%);
					    -ms-transform: translateX(-50%);
					    transform: translateX(-50%);
					    border: 6px solid transparent;
					    border-bottom: 8px solid rgba(0, 0, 0, 0.8);
				        top: -14px;
				    }
				    li {
				    	.sub_menu_item, .sub-menu {
					    	left: 100%;
					    	top: 0;
					    	&:after {
					    		content: none;
					    	}
						    &.revert {
						    	left: auto;
						    	right: 100%;
						    }
					    }
				    }
			    }
			    &:hover {
			    	a, span {
			    		font-weight: bold;
			    	}
			    }
			}
			/* .sub_menu_arrow {
				&:after {
				    top: 0;
				    right: -20px;
				}
			} */
			a {
				text-transform: uppercase;
			}
			i {
				vertical-align: middle;
			    margin-left: 10px;
			    margin-right: 10px;
			}
		}
		.nav_menu .menutop > .menu-item-has-children {
			&:after {
				font-size: 18px;
				-webkit-transform: rotate(90deg);
				-ms-transform: rotate(90deg);
				transform: rotate(90deg);
			    top: 0;
			    right: -20px;
			}
		}
		.search {
			margin-right: 30px;
			margin-left: 30px;
			/* @extend.animate; */
			i {
				vertical-align: middle;
				cursor: pointer;
			}
			input {
				font-family: $branding-light;
				font-size: 40px;
				width: 100%;
				border: none;
				&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  color: $black;
				}
				&::-moz-placeholder { /* Firefox 19+ */
				  color: $black;
				}
				&:-ms-input-placeholder { /* IE 10+ */
				  color: $black;
				}
				&:-moz-placeholder { /* Firefox 18- */
				  color: $black;
				}
			}
			.close_search_icon {
				color: $accent;
				font-size: 22px;
				margin-right: 112px;
			    margin-left: 20px;
			}
			.search_field {
				display: none;
			}
			&.active {
				background: $white;
			    position: absolute;
			    top: 0;
			    bottom: 0;
			    left: 0;
			    right: 0;
			    margin: 0;
				.search_field {
					display: -webkit-box;
					display: -webkit-flex;
					display: -ms-flexbox;
					display: flex;
				}
			    .search_icon {
			    	display: none;
			    }

			}
		}
		.lang {
			text-transform: uppercase;
		}
	}
	.rtl {
		.header {
			/* .nav_menu {
				.sub_menu_arrow {
					&:after {
						right: auto;
						left: -20px;
					}
				}
			} */
			.nav_menu .menutop > .menu-item-has-children {
				&:after {
					right: auto;
					left: -20px;
				}
			}
		}
	}
/*---SLIDER---*/
	.slider {
		.slider_item {
			height: 640px;
			max-height: 100vh;
			position: relative;
		}
		.slider_text {
			position: absolute;
			top: 50%;
			width: 100%;
			-webkit-transform: translateY(-50%);
			-ms-transform: translateY(-50%);
			transform: translateY(-50%);
			text-align: center;
			color: $white;
			padding-left: 15px;
			padding-right: 15px;
			p {
				font-size: 25px;
				line-height: 35px;
				max-width: 580px;
				width: 100%;
				margin: auto;
			}
			.up_bold {
				text-transform: uppercase;
				font-weight: bold;
			}
		}
		.slider-nav-btn {
			position: absolute;
		    top: 100px;
		    bottom: 60px;
		    width: 100px;
			color: $white;
			@extend.animate;
			&:hover {
				background: rgba(0, 0, 0, 0.3);
			}
			&:after {
				position: absolute;
			    top: 50%;
			    left: 50%;
			    -webkit-transform: translate(-50%,-50%);
			    -ms-transform: translate(-50%,-50%);
			    transform: translate(-50%,-50%);
			}
			&.prew-btn {
				left: 0;
				&:after {
					content: '\f104';
					font-size: 40px;
					font-family: 'Font Awesome 5 Free';
				    font-weight: 900;
			    }
			}
			&.next-btn {
				right: 0;
				&:after {
					content: '\f105';
					font-size: 40px;
					font-family: 'Font Awesome 5 Free';
				    font-weight: 900;
			    }
			}
		}
	}
