@media (max-width: 1259px) {
	main {
		padding-top: 100px;
		min-height: -webkit-calc(100vh - 153px);
		min-height: calc(100vh - 153px);
	}
	.header {
		background: $white;
	    /* position: fixed; */
	    border-bottom: 1px solid $black;
		.sandwich {
			display: block;
		}
		.menu_wrap {
			padding: 15px;
			background: rgba(35, 31, 32, 0.95);
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap;
			height: auto;
			top: 0;
			left: 0;
			right: 0;
			position: absolute;
			visibility: hidden;
			opacity: 0;
			color: $white;
			&.active {
				opacity: 1;
				visibility: visible;
			}
		}
		.lang {
			-webkit-box-ordinal-group: 2;
			-webkit-order: 1;
			-ms-flex-order: 1;
			order: 1;
			width: 100%;
		    margin-bottom: 35px;
		    font-size: 20px;
		    margin-top: 25px;
			a.active {
				color: $accent;
			}
		}
		.nav_menu {
			.sub_menu_item_block, .menu-item-has-children {
				&:after {
					-webkit-transform: rotate(90deg);
					-ms-transform: rotate(90deg);
					transform: rotate(90deg);
					/* top: 20px; */
				}
			}
		}
		.search {
			-webkit-box-ordinal-group: 3;
			-webkit-order: 2;
			-ms-flex-order: 2;
			order: 2;
			width: 100%;
		    margin: 0;
		    position: relative;
			.search_field {
				display: block;
				padding: 0;
			}
			input {
				font-family: $branding;
				color: $black;
				height: 55px;
				line-height: 55px;
				font-size: 20px;
				padding-left: 20px;
				padding-right: 20px;
			}
			i {
				position: absolute;
				color: $black;
			    right: 20px;
			    top: 16px;
			    font-size: 22px;
			}
			.close_search_icon {
				display: none;
			}
		}
		.nav_menu {
			-webkit-box-ordinal-group: 4;
			-webkit-order: 3;
			-ms-flex-order: 3;
			order: 3;
			margin-top: 20px;
			width: 100%;
		    /* max-height: -webkit-calc(100vh - 190px);
		    max-height: calc(100vh - 190px);
		    overflow: auto; */
			li {
				display: block;
				margin: 0;
				text-align: center;
				border-bottom: 1px solid #333131;
				a, span {
					text-align: center;
					padding-top: 20px;
					padding-bottom: 20px;
					display: inline-block;
				}
				i {
					position: absolute;
					top: 24px;
					right: 0;
				}
				&:last-of-type {
					border: none;
				}
				.sub_menu_item, .sub-menu {
					position: static;
					border-top: 1px solid #333131;
					li {
						width: 100%;
					}
					&:before, &:after {
						content: none;
					}
				}
				&:hover {
					span, a {
						font-weight: normal;
					}
				}
				&.opened {
					color: $accent;
				}
			}
			/* .sub_menu_arrow {
				&:after {
					right: 15px;
				    top: 20px;
				}
			} */
		}
		.nav_menu .menutop > .menu-item-has-children {
			&:after {
				right: 15px;
			    top: 20px;
			}
		}
	}
	.rtl {
		.header {
			.search {
				i {
					right: auto;
					left: 20px;
				}
			}
			.nav_menu {
				/* .sub_menu_arrow:after {
					left: 20px;
				} */
				.sub_menu_item_block:after, .menu-item-has-children:after {
					left: 25px;
	    			right: auto;
				}
			}
			.nav_menu .menutop > .menu-item-has-children {
				&:after {
					left: 20px;
				}
			}
		}
	}
	.go_up {
		display: block;
		height: 55px;
		width: 55px;
	    line-height: 55px;
		text-align: center;
		background: #413b35;
	    color: #fff;
        position: fixed;
	    right: 15px;
	    bottom: 15px;
