			content: '\f106';
			/* font: 30px FontAwesome;*/
			font-size: 30px;
			font-family: 'Font Awesome 5 Free';
		    font-weight: 900;
	    }
	}
	/* .slider_block {
		margin-top: 100px;
	} */
	.info_blocks {
		.info_items_block {
			.info_item_descr,
			.info_iten_img {
				height: 50vw;
			}
		}
	}
	.advantages {
		.advantage_block {
			height: auto;
			width: -webkit-calc(100% / 3);
			width: calc(100% / 3);
			padding: 15px;
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
		    -webkit-box-align: center;
		    -webkit-align-items: center;
		    -ms-flex-align: center;
		    align-items: center;
		}
	}
	/* .footer_item {
		width: -webkit-calc(100% / 3);
		width: calc(100% / 3);
		text-align: center;
		&:nth-of-type(2) {
			width: -webkit-calc(100% / 3);
			width: calc(100% / 3);
		}
	} */
	.notice_text_wrap, .rules_list {
		-webkit-column-count: auto;
		-moz-column-count: auto;
		column-count: auto;
		p {
			margin-bottom: 20px;
		}
	}
	.rules_block {
		.page_description_text {
			p {
				margin-bottom: 20px;
			}
		}
	}
	.tabs {
		overflow-x: auto;
	    overflow-y: hidden;
	    li {
	    	height: auto;
		    font-size: 14px;
   			padding: 10px;
	    }
		/* &:before {
			content: '\f104';
