* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-family: "Branding", Arial, sans-serif;
	font-size: 16px;
	margin: 0;
	padding: 0;
	outline: none;
}
li {
	/* list-style-type: none; */
}
a {
	color: inherit;
	text-decoration: none;
}
a:hover {
	color: inherit;
	text-decoration: none;
}
a:focus {
	color: inherit;
	text-decoration: none;
}